/**
 * 重置标签的CSS样式
 */

/* CSS Document */

html,
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span,
form,
fieldset,
table,
td,
img,
div,
dl,
dt,
dd,
input,
a,
header,
section,
footer,
nav {
  margin:0;
  padding:0;
  outline-style: none;
}

body {
  min-width: 1349px;
  font-size:12px;
  font-family: "MicrosoftYaHei";
}

img {
  border:none;
}

li {
  list-style:none;
}

s {
  text-decoration: none;
}

input,
select,
textarea {
  outline:none;
  border:none;
  background:none;
}

textarea {
  resize:none;
}

a {
  text-decoration:none;
  color:#666;
}

em,
i {
  font-style: normal;
}

.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1; /*IE/7/6*/
}

.canot input{
  background: #ddd;
}

